*{
  box-sizing: border-box;
  margin: 0;
  padding: 0 ;
  font-family: 'Source Sans Pro', sans-serif;
  transition: all 0.9s ease-out;
  /* animation:0.10s ease-out forwards; */
}

.centered {
  margin: 3rem auto;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

