.overlay {
    /* Height & width depends on how you want to reveal the overlay (see JS below) */   
    height: 100%;
    width: 0;
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    background-color: rgb(0,0,0); /* Black fallback color */
    background-color: rgba(0,0,0, 0.9); /* Black w/opacity */
    overflow-x: hidden; /* Disable horizontal scroll */
    transition: 0.5s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
  }
  
  /* Position the content inside the overlay */
 .overlay-content {
    position: relative;
    top: 25%; /* 25% from the top */
    width: 100%; /* 100% width */
    text-align: center; /* Centered text/links */
    margin-top: 30px; /* 30px top margin to avoid conflict with the close button on smaller screens */
  }

  .overlay a {
    padding: 8px;
    text-decoration: none;
    font-size: 36px;
    color: #818181;
    display: block; /* Display block instead of inline */
    transition: 0.3s; /* Transition effects on hover (color) */
  }

  .overlay a:hover, .overlay a:focus {
    color: #f1f1f1;
  }
  
  /* Position the close button (top right corner) */
  .overlay .closebtn {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
  }
  
  /* When the height of the screen is less than 450 pixels, change the font-size of the links and position the close button again, so they don't overlap */
  @media screen and (max-height: 450px) {
    .overlay a {font-size: 20px}
    .overlay .closebtn {
      font-size: 40px;
      top: 15px;
      right: 35px;
    }
  }

.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
     background-color: #9370DB;
    /* background-color: #CBC3E3; */
    color:white;
    position: relative;  /*this will fix the navbar position*/
    width:100%; 
    height:100px;
    z-index:1;  /* Our Navbar disappeared after completing the home page and this style  puts it back into place */

}


h1{
    margin-left: 1rem;
}

h1 .title{
font-size: 1.7rem;
font-weight: 200;
padding-left: 0.5rem;
text-decoration: none;
}

.header h1:hover{
   
    color:#DCDCDC;
    font-weight: 400;
    /* text-decoration: underline; */
}

.nav-menu{     
    display: flex;                   /*this style will show our pages links into row format */
   /* justify-content: space-between;   this style will add spaces b/w the pages links */
}

.nav-menu li{          /* this style will be applied on all 'list' items*/
     padding:0 1rem;     /*top-to-bottom  left-to-right    this style will add spaces b/w the pages link names */
     margin-right: 1rem;
}

.nav-menu a{    /* this style will be applied on all <Link> elements */
    font-size: 1.2rem;
    font-weight: 500;
}

.nav-menu a:hover{   
   
    color:#DCDCDC;
    font-weight: 900;
    text-decoration: underline;
}

.hamburger{
    display: none;
}

.hamburger:hover{
    /* color: plum; */
    color:#DCDCDC
    
}

@media screen and (max-width: 1028px){
    .nav-menu{
        flex-direction: column;
        justify-content: center; 
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color:#9370DB;
        position: absolute;
        top:0;
        left:-100%; /* this style will keep our navbar pagelinks hidden until user clicks on menuicon */
        z-index: -1;
        transition: 0.3s
    }
    .nav-menu.active{  /* this style will appear our navbar pagelinks when user clicks on menuicon */
        left:0;
       
    }
    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu a{
        font-size:  2rem;
    }
    .hamburger{
        display: initial;
        padding-right: 1.3rem;
        padding-top: 0.5rem;
    }
    h1{
        padding-left: 0.1rem;
    }
   
}

@media screen and (max-width: 600px){
    h1 .title{
        visibility: hidden;
        transition: none;
    }
    .hamburger{
        padding-right: 0.4rem;
    }
}

@media screen and (max-width: 526px){
     .title-heading{
       margin-top: 2.3rem;
       transition: none;
    }
}

