
/* The below code is for spinner*/
.spinner {
    display: inline-block;
    width: 80px;
    height: 80px;
  }
  .spinner:after {
    content: ' ';
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid  #9370DB;
    border-color:  #9370DB transparent  #9370DB transparent;
    animation: spinner 1.2s linear infinite;
  }

  /* The below code is for loader*/
  .loader {
    border: 10px solid #f3f3f3;
    border-top: 10px solid  #9370DB;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    animation: spinner 1.2s linear infinite;
  }

  @keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }